section {
    background-color: var(--primary-gray);
    @include flex-position($x: space-around);
    flex-direction: column;

    h2 {
        color: var(--primary-color);
        padding-bottom: 2px;
        border-bottom: 3px solid var(--primary-color);
        border-radius: 2px;
    }
}

.section-row {
    height: 40%;
    width: 80%;
    @include flex-position;
    flex-direction: row-reverse;
    background: var(--bg-color);
    border-radius: 10px;
    @include box-shadow;
    overflow: auto;

    img {
        height: 100%;
    }

    p {
        color: #1a1a1d;
        padding: 25px;
        text-align: right;

        .highlight {
            color: var(--primary-color)
        }
    }

    &.rv {
        flex-direction: row;
    }
}


@mixin mq-sections(){
    section {
        height: 280vh;
        background-color: var(--primary-white);

        h2 {
            text-align: center;
            max-width: 80%;
            padding-bottom: 5px;

        }
    }

    .section-row {
        flex-direction: column;
        height: auto;
        box-shadow: none;
        border-radius: 0;
        width: 100%;
        
        &.rv {
            flex-direction: column;
        }

        img {
            height: auto;
            width: 100%;
        }

        p {
            font-size: 1.5rem;
        }
    }
}